<template>
  <div class="shopcart">
    <van-nav-bar
      :right-text="isManage ? '完成' : '管理'"
      :fixed="true"
      @click-right="manage"
      v-if="isLogin && allShocarData.length > 0"
    />
    <van-nav-bar right-text="管理" :fixed="true" v-else />
    <template v-if="allShocarData.length > 0">
      <div class="shopcart-box">
        <van-list
          :finished="isFinished"
          finished-text="别拉我啦，真的一滴也没有了"
          v-model="isLoading"
          @load="loadData"
          class="shop-loading"
        >
          <van-swipe-cell
            v-for="(item, index) in shopcartData"
            :key="index"
            :disabled="!isManage"
          >
            <div class="content-item">
              <div class="shopcart-checkbox">
                <van-checkbox
                  v-model="item.isChecked"
                  @change="simpleSelect(item)"
                ></van-checkbox>
              </div>
              <div class="pro-img" @click="goPage(item.pid)">
                <img class="auto-img" :src="item.small_img" />
              </div>
              <div class="pro-desc">
                <div class="pro-name">
                  <div class="clearfix">
                    <div class="ch-name fl">{{ item.name }}</div>
                    <div class="rule fl">{{ item.rule }}</div>
                  </div>
                  <div class="en-name">{{ item.enname }}</div>
                </div>

                <div class="pro-price">
                  <div class="fl">￥{{ item.price }}</div>
                  <div class="fr clearfix">
                    <span class="icon fl left" @click="decrease(item)"></span>
                    <input
                      class="fl pro-count"
                      type="text"
                      v-model="item.count"
                      readonly
                    />
                    <span class="icon fl right" @click="increase(item)"></span>
                  </div>
                </div>
              </div>
            </div>
            <template #right>
              <van-button
                square
                text="删除"
                type="danger"
                class="delete-button v-btn"
                @click="removeOneShopcart(item.sid, index)"
              />
            </template>
          </van-swipe-cell>
        </van-list>
      </div>

      <!-- 提交订单 -->
      <van-submit-bar
        :price="total"
        button-text="提交订单"
        button-type="info"
        :safe-area-inset-bottom="true"
        v-show="!isManage"
        @submit="commit"
        :disabled="total == 0 ? true : false"
      >
        <!-- button-type='info'修改按钮为蓝色 -->
        <van-checkbox v-model="isAllSelect" @click="allSelect"
          >全选</van-checkbox
        >
      </van-submit-bar>

      <!-- 删除 -->
      <van-submit-bar
        class="v-submit"
        button-text="删除"
        button-type="danger"
        :safe-area-inset-bottom="true"
        v-show="isManage"
        @submit="removeMoreShopcart"
      >
        <!-- button-type='info'修改按钮为蓝色 -->
        <van-checkbox v-model="isAllSelect" @click="allSelect"
          >全选</van-checkbox
        >
      </van-submit-bar>
    </template>

    <template v-else>
      <div>
        <div class="not-shop" v-if="isLogin">
          <div class="not-shop-icon">
            <van-icon name="shopping-cart-o"></van-icon>
          </div>
          <div>购物车暂时没有商品，快去逛逛吧</div>
        </div>
        <div v-else class="not-shop">
          <div class="login-text" @click="goPage">点击登陆，查看购物车</div>
        </div>
      </div>
      <div class="think">
        <div class="text">———— 猜你喜欢 ————</div>
        <div class="like-box clearfix">
          <div
            :class="[index % 2 == 0 ? 'a' : 'b']"
            v-for="(item, index) in currentProduct"
            :key="index"
            @click="goPage(item.pid)"
          >
            <div>
              <img class="auto-img" :src="item.smallImg" />
            </div>
            <div class="title">{{ item.name }}</div>
            <div class="clearfix">
              <span class="fl price">￥{{ item.price }}</span>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { utils } from "../utils/utils";
export default {
  name: "Shopcart",
  data() {
    return {
      //触底加载提示
      isLoading: false,

      //全选
      isAllSelect: false,
    };
  },
  created() {
    //置空数据
    this.$store.commit("shopcartModule/emptyData");
    this.getTypeProduct();

    //获取购物车所有商品
    this.getAllShopcartProduct();
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    ...mapState("shopcartModule", [
      "allShocarData",
      "shopcartData",
      "isFinished",
      "count",
      "isManage",
      "total",
      "currentProduct",
    ]),
  },
  methods: {
    //获取商品类型的商品数据
    getTypeProduct() {
      //请求参数
      let params = {
        appkey: this.appkey,
      };
      params.value = 1;
      params.key = "isHot";

      //加载提示
      this.$toast.loading({
        //禁止背景点击
        forbidClick: true,
        //展示时长为0禁止自动关闭
        duration: 0,
        //内容
        message: "正在加载...",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((d) => {
          //关闭加载提示
          this.$toast.clear();

          if (d.data.code == 500) {
            this.$store.commit("shopcartModule/changeData", {
              key: "currentProduct",
              value: d.data.result,
            });
            //输出当前列表的商品数据
          }
        })
        .catch((err) => {
          //关闭加载提示
          this.$toast.clear();
        });
    },

    //查看商品详情
    goPage(pid) {
      if (typeof pid == "object") {
        this.$router.push({ name: "Login" });
      } else {
        this.$router.push({ name: "Detail", params: { pid } });
      }
    },

    //获取购物车所有商品
    getAllShopcartProduct() {
      //加载提示
      this.$toast.loading({
        //禁止背景点击
        forbidClick: true,
        //展示时长为0禁止自动关闭
        duration: 0,
        //内容
        message: "加载中...",
      });

      //获取token字符串
      let tokenString = localStorage.getItem("_t");
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((d) => {
          //清除加载
          this.$toast.clear();

          this.$store.commit("changeData", {
            key: "isLogin",
            value: d.data.code != 700,
          });
          if (d.data.code == 700) {
            return;
          }

          if (d.data.code == 5000) {
            //添加判断勾选属性
            d.data.result.forEach((v) => {
              v.isChecked = false;
            });
            this.$store.commit("shopcartModule/changeData", {
              key: "allShocarData",
              value: d.data.result,
            });
          }
        })
        .catch((err) => {
          //清除加载
          this.$toast.clear();
        });
    },

    //加载商品数据
    loadData() {
      setTimeout(() => {
        this.isLoading = false;

        if (this.shopcartData.length >= this.allShocarData.length) {
          //没有数据可加载
          this.$store.commit("shopcartModule/changeData", {
            key: "isFinished",
            value: true,
          });
          return;
        }
        this.$store.commit("shopcartModule/loadShopcartData");
        this.isAllSelect = false;
      }, 1000);
    },

    //修改购物车商品数量
    modifyShopcartCount(sid, count) {
      //加载提示
      this.$toast.loading({
        //禁止背景点击
        forbidClick: true,
        //展示时长为0禁止自动关闭
        duration: 0,
        //内容
        message: "处理中...",
      });

      //获取token字符串
      let tokenString = localStorage.getItem("_t");
      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid,
          count,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          //清除加载
          this.$toast.clear();

          this.sum();
        })
        .catch((err) => {
          //清除加载
          this.$toast.clear();
        });
    },

    //增加商品数量
    increase(item) {
      ++item.count;
      this.modifyShopcartCount(item.sid, item.count);
    },

    //减少商品数量
    decrease(item) {
      if (item.count == 1) {
        return;
      }
      --item.count;
      this.modifyShopcartCount(item.sid, item.count);
    },

    //管理商品
    manage() {
      this.$store.commit("shopcartModule/changeData", {
        key: "isManage",
        value: !this.isManage,
      });
    },

    //全选
    allSelect() {
      this.shopcartData.forEach((v) => {
        v.isChecked = this.isAllSelect;
      });
    },

    //单选
    simpleSelect(item) {
      this.sum();
      if (!item.isChecked) {
        //没全选
        this.isAllSelect = false;
        return;
      }

      for (let i = 0; i < this.shopcartData.length; i++) {
        if (!this.shopcartData[i].isChecked) {
          //没全选
          this.isAllSelect = false;
          return;
        }
      }
      this.isAllSelect = true;
    },

    //删除单个商品
    removeOneShopcart(sid) {
      let sids = JSON.stringify([sid]);

      this.removeShopcart(sids)
        .then((d) => {
          //清除加载
          this.$toast.clear();

          this.$store.commit("shopcartModule/removeShopcartData", [sid]);
          this.sum();
          this.getAllShopcartProduct();
        })
        .catch((err) => {
          //清除加载
          this.$toast.clear();
        });
    },

    //删除多个商品
    removeMoreShopcart() {
      //获取勾选的商品
      let sids = [];
      this.shopcartData.forEach((v) => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });
      if (sids.length == 0) {
        this.$toast({
          message: "没有选择商品",
        });
        return;
      }
      this.removeShopcart(JSON.stringify(sids))
        .then((d) => {
          //清除加载
          this.$toast.clear();
          this.$store.commit("shopcartModule/removeShopcartData", sids);
          this.sum();
          this.getAllShopcartProduct();
        })
        .catch((err) => {
          //清除加载
          this.$toast.clear();
        });
    },

    //删除商品
    removeShopcart(sids) {
      //加载提示
      this.$toast.loading({
        //禁止背景点击
        forbidClick: true,
        //展示时长为0禁止自动关闭
        duration: 0,
        //内容
        message: "处理中...",
      });

      //获取token字符串
      let tokenString = localStorage.getItem("_t");
      // let sids=JSON.stringify(pids)

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids,
        },
        transformRequest: utils.transformRequest,
      });
    },

    //计算订单总价
    sum() {
      let total = 0;
      this.shopcartData.forEach((v) => {
        if (v.isChecked) {
          total += v.price * v.count * 100;
        }
      });
      this.$store.commit("shopcartModule/changeData", {
        key: "total",
        value: total,
      });
    },

    //提交订单
    commit() {
      //获取勾选商品的购物车sid
      let sids = [];
      this.shopcartData.forEach((v) => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });
      this.$router.push({ name: "Pay", query: { sids: sids.join("-") } });
    },
  },
};
</script>

<style lang="less" scoped>
.shopcart {
  padding-top: 46px;
  padding-bottom: 40px;
  .not-shop {
    font-size: 22px;
    margin-top: 50px;
    color: #e0e0e0;
    text-align: center;
  }
  .login-text {
    color: #165dad;
  }
  .not-shop-icon {
    font-size: 60px;
  }
  .shopcart-box {
    background-color: #fff;
    .shop-loading {
      padding-bottom: 70px;
    }
    .v-btn {
      height: 100%;
    }
  }
  .v-submit {
    .van-checkbox {
      margin-right: auto;
    }
  }
  .content-item {
    height: 80px;
    display: flex;
    padding: 10px;
    border-bottom: 1px solid #f0f0f0;
    .shopcart-checkbox {
      margin-top: 30.5px;
      margin-right: 10px;
    }
    .pro-img {
      width: 80px;
    }
    .pro-desc {
      width: calc(~"100% - 80px - 10px - 26px");
      margin-left: 10px;
    }
    .pro-name {
      height: 54px;
      .clearfix {
        height: 20.8px;
        .ch-name {
          font-size: 16px;
          color: #444;
        }
        .rule {
          font-size: 14px;
          margin-left: 8px;
          line-height: 20.8px;
          color: #c2c2c2;
        }
      }
      .en-name {
        font-size: 12px;
        margin-top: 5px;
        color: #888;
      }
    }
    .pro-price {
      height: 24px;
      font-size: 15px;
      line-height: 24px;
      color: #165dad;
      .clearfix {
        .icon {
          width: 24px;
          height: 24px;
          border-radius: 50%;
          background-position: center center;
          background-size: cover;
          background-repeat: no-repeat;

          &.left {
            background-image: url("../assets/edd.png");
          }
          &.right {
            background-image: url("../assets/add.png");
          }
        }
        .pro-count {
          height: 24px;
          width: 30px;
          margin: 0 5px;
          text-align: center;
        }
      }
    }
  }
  .van-submit-bar__price {
    color: #165dad;
  }
  .van-submit-bar {
    bottom: 49.98px;
  }
  .think {
    margin-top: 60px;
  }
  .text {
    font-size: 16px;
    text-align: center;
    color: #a8a8a8;
  }
  .like-box {
    > div {
      float: left;
      width: calc(~"50% - 5px - 20px");
      background-color: #fff;
      padding: 10px;
      margin-bottom: 10px;
      &.a {
        margin-right: 5px;
        border-bottom: 1px solid #e8e8e8;
      }
      &.b {
        border-bottom: 1px solid #e8e8e8;
        margin-left: 5px;
      }
      .title {
        padding: 10px 0;
        color: #444;
        font-size: 16px;
      }
      .price {
        font-size: 14px;
        color: #165dad;
        line-height: 24px;
      }
      .delete {
        height: 24px;
        width: 24px;
        .v-icon {
          color: #e0e0e0;
          font-size: 20px;
        }
      }
    }
  }
}
</style>